"use client"

import { useState } from "react"
import Image from "next/image"
import { X, ChevronLeft, ChevronRight } from "lucide-react"

const images = [
  { src: "/beautiful-mountain-landscape-sunrise-brazil-serra.jpg", alt: "Vista da serra ao nascer do sol" },
  { src: "/cozy-pousada-exterior-with-garden-and-nature.jpg", alt: "Área externa da pousada" },
  { src: "/rustic-breakfast-table-with-fresh-fruits-and-coffe.jpg", alt: "Café da manhã" },
  { src: "/comfortable-bedroom-with-natural-light-and-plants.jpg", alt: "Quarto confortável" },
  { src: "/green-forest-trail-hiking-nature.jpg", alt: "Trilha na natureza" },
  { src: "/sunset-view-from-mountain-with-orange-sky.jpg", alt: "Pôr do sol na serra" },
  { src: "/garden-with-tropical-plants-and-hammock.jpg", alt: "Jardim com rede" },
  { src: "/birds-in-nature-atlantic-forest-brazil.jpg", alt: "Pássaros na natureza" },
]

export default function Gallery() {
  const [selectedImage, setSelectedImage] = useState<number | null>(null)

  const openLightbox = (index: number) => setSelectedImage(index)
  const closeLightbox = () => setSelectedImage(null)
  const prevImage = () => setSelectedImage((prev) => (prev === 0 ? images.length - 1 : (prev ?? 0) - 1))
  const nextImage = () => setSelectedImage((prev) => (prev === images.length - 1 ? 0 : (prev ?? 0) + 1))

  return (
    <section id="galeria" className="py-20 lg:py-32 bg-muted">
      <div className="container mx-auto px-4">
        <div className="text-center mb-16">
          <span className="text-primary text-sm tracking-[0.2em] uppercase font-medium">Galeria</span>
          <h2 className="text-3xl md:text-4xl lg:text-5xl font-serif font-bold mt-2 mb-4 text-foreground">
            Momentos na Monte Horebe
          </h2>
          <p className="text-muted-foreground max-w-2xl mx-auto">
            Conheça um pouco da nossa estrutura e das paisagens deslumbrantes que você encontrará aqui.
          </p>
        </div>

        <div className="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-4">
          {images.map((image, index) => (
            <button
              key={index}
              onClick={() => openLightbox(index)}
              className="aspect-square relative overflow-hidden rounded-lg group cursor-pointer"
            >
              <Image
                src={image.src || "/placeholder.svg"}
                alt={image.alt}
                fill
                className="object-cover transition-transform duration-500 group-hover:scale-110"
              />
              <div className="absolute inset-0 bg-black/0 group-hover:bg-black/30 transition-colors flex items-center justify-center">
                <span className="text-white opacity-0 group-hover:opacity-100 transition-opacity font-medium">Ver</span>
              </div>
            </button>
          ))}
        </div>
      </div>

      {/* Lightbox */}
      {selectedImage !== null && (
        <div className="fixed inset-0 z-50 bg-black/90 flex items-center justify-center p-4">
          <button
            onClick={closeLightbox}
            className="absolute top-4 right-4 text-white hover:text-gray-300 transition-colors"
            aria-label="Fechar"
          >
            <X className="w-8 h-8" />
          </button>
          <button
            onClick={prevImage}
            className="absolute left-4 text-white hover:text-gray-300 transition-colors"
            aria-label="Anterior"
          >
            <ChevronLeft className="w-10 h-10" />
          </button>
          <button
            onClick={nextImage}
            className="absolute right-4 text-white hover:text-gray-300 transition-colors"
            aria-label="Próxima"
          >
            <ChevronRight className="w-10 h-10" />
          </button>
          <div className="relative max-w-4xl max-h-[80vh] w-full h-full">
            <Image
              src={images[selectedImage].src || "/placeholder.svg"}
              alt={images[selectedImage].alt}
              fill
              className="object-contain"
            />
          </div>
        </div>
      )}
    </section>
  )
}
